'use strict';

// MAIN //

/**
* Inserts an element into an array.
*
* @param {Array} x - input array
* @param {integer} index - element index
* @param {*} value - value to insert
* @returns {Array} mutated input array
*
* @example
* var x = [ 1, 1, 2, 3, 3 ];
*
* var y = insertAt( x, -3, 4 );
* // returns [ 1, 1, 4, 2, 3, 3 ]
*
* var bool = ( x === y );
* // returns true
*
* @example
* var x = [ 1, 1, 2, 3, 3 ];
*
* var y = insertAt( x, -1, 4 );
* // returns [ 1, 1, 2, 3, 4, 3 ]
*
* var bool = ( x === y );
* // returns true
*
* @example
* var x = [ 1, 1, 2, 3, 3 ];
*
* var y = insertAt( x, x.length, 4 );
* // returns [ 1, 1, 2, 3, 3, 4 ]
*
* var bool = ( x === y );
* // returns true
*/
function insertAt( x, index, value ) {
	var len;
	var i;

	len = x.length;
	if ( index < 0 ) {
		index += len;
		if ( index < 0 ) {
			index = 0;
		}
	} else if ( index > len ) {
		index = len;
	}
	x.push( x[ len-1 ] );
	for ( i = len-2; i >= index; i-- ) {
		x[ i+1 ] = x[ i ];
	}
	x[ index ] = value;
	return x;
}


// EXPORTS //

export default insertAt;
